import classNames from 'classnames/bind';
import styles from '../Styles/ManageOrder.module.scss';

const cx = classNames.bind(styles);

function ManageOrder({ dataCart, handleShowModalEdit }) {
    return (
        <div className={cx('manage-product')}>
            <h2>Quản Lý Đơn Hàng</h2>
            <div className="table-responsive">
                <table className="table table-bordered border-primary ">
                    <thead>
                        <tr>
                            <th scope="col">Người Dùng</th>
                            <th scope="col">Tên Đơn Hàng</th>
                            <th scope="col">Địa Chỉ </th>
                            <th scope="col">Size</th>
                            <th scope="col">Số Lượng</th>
                            <th scope="col">Tình Trạng</th>
                            <th scope="col">Hành Động</th>
                        </tr>
                    </thead>
                    <tbody>
                        {dataCart.map((item) => (
                            <tr key={item._id}>
                                <td>{item.user}</td>
                                <td>{item.nameProduct}</td>
                                <td>{item.address}</td>
                                <td>{item.size}</td>
                                <td>{item.quantity}</td>
                                <td>{item.tinhtrang ? 'Đã Giao Thành Công ' : 'Người Bán Đang Chuẩn Bị Hàng'}</td>
                                <td style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                    <button
                                        onClick={() => handleShowModalEdit(item._id, item.address)}
                                        type="button"
                                        className="btn btn-primary"
                                    >
                                        Xác Nhận Đơn Hàng
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default ManageOrder;
